<template>
    <transition name="flag">
	<div class="fooddetail" v-show="showFlag" ref ="foodScroll">
	  <div class="food-content">
	  	<div class="image-loader">
	  		<img :src="fooddetail.image" alt="">
	  		<div class="back" @click="hide">
	  			<i class="icon-arrow_lift"></i>
	  		</div>	  		
	  	</div>
	  	<div class="content">
	  		<div class="title">{{fooddetail.name}}</div>
	  		<div class="detail">
	  			<span class="sell-count">月售{{fooddetail.sellCount}}份</span>
	  			<span class="rating">好评率{{fooddetail.rating}}</span>
	  		</div>
	  	    <div class="price">
	  	    	<span class="now">￥{{fooddetail.price}}</span>
	  	    	<span class="old" v-show = "fooddetail.oldPrice">￥{{fooddetail.oldPrice}}</span>
	  	    </div>
        <div class="cartcontrol-wrapper">
          <cartcontrol :foods="fooddetail"></cartcontrol>
        </div>
        <div class="buy" v-show ="!fooddetail.count" @click="addFrist">加入购物车</div>	  	    
	  	</div>
	    <split v-show="fooddetail.info"></split>
      <div class="info" v-show="fooddetail.info">
        <h1 class="title">商品信息</h1>
        <p class="text">{{fooddetail.info}}</p>
      </div>
       <split v-show="fooddetail.info"></split>
       <div class="rating">
         <h1 class="title">商品评价</h1>
         <ratingselect :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="fooddetail.ratings" ref="ratingselect" @selectF="getF" @selectG="getG"></ratingselect>
         <div class="rating-wrapper"> 
            <ul v-show="fooddetail.ratings && fooddetail.ratings.length">
              <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in fooddetail.ratings" class="rating-item border-1px">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img :src="rating.avatar" alt="" width="12" height="12" class="avatar">
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up':rating.rateType === 0,'icon-thumb_down': rating.rateType === 1}"></span>{{rating.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-show = "!fooddetail.ratings || !fooddetail.ratings.length">暂无评价</div>
         </div>
       </div>
	  </div>
	</div>
	</transition>
</template>
<script>
  import BScroll from 'better-scroll';
  import Vue from 'vue';
  import cartcontrol from '../cartcontrol/cartcontrol';
  import split from '../split/split';
  import ratingselect from '../ratingSelect/ratingSelect';
  import {formatDate} from '../../common/js/date';

  const ALL = 2;
  export default {
    props: {
      fooddetail: {
        type: Object
      }
    },
    data () {
      return {
        showFlag: false,
        selectType: ALL,
        onlyContent: true,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      };
    },
    components: {
      cartcontrol,
      split,
      ratingselect
    },
    filters: {
      formatDate (time) {
        let date = new Date(time);
        return formatDate(date, 'YYYY-MM-DD hh:mm');
      }
    },
    methods: {
      getFlag (type) {
        this.showFlag = true;
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.foodScroll, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      getF (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      getG (con) {
        this.onlyContent = con;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      hide () {
        this.showFlag = false;
      },
      addFrist (event) {
        if (!event._constructed) {
          return;
        }
        this.$emit('cartAdd', event.target);
        Vue.set(this.fooddetail, 'count', 1);
      },
      needShow (type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return this.selectType === type;
        }
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
.fooddetail
  position:fixed
  left: 0
  top: 0
  bottom: 48px
  z-index: 30
  width: 100%
  background:#fff
  transition:all 0.2s linear
  transform: translate3d(0,0,0)
  &.flag-enter
  &.flag-leave
    transform:translate3d(100%, 0, 0)
  .image-loader
	  position:relative
	  width:100%
	  height:0
	  padding-top:100%
	  img
	    position:absolute
	    top: 0
	    left: 0
	    width:100%
	    height:100%
	  .back
	    position:absolute
	    top: 10px
	    left: 0
	    .icon-arrow_lift
	      display:block
	      padding:10px
	      font-size:20px
	      color:#fff
  .content
    position:relative
    padding:18px
    .title
      line-height:14px
      font-size:14px
      margin-bottom:8px
      font-weight:700
      color:rgb(7,17,27)
    .detail
      margin-bottom:18px
      line-height:10px
      font-size:0
      .sell-count
      .rating
        font-size:10px
        color:rgb(147,153,159)
      .sell-count
        margin-right:12px
    .price
      font-weight:700
      line-height:24px
      .now
        color:rgb(240,20,20)
        font-size:14px
        margin-top:8px
      .old
        font-size:10px
        color:rgb(147,153,159)
        text-decoration: line-through
    .cartcontrol-wrapper
      position:absolute
      right:12px
      bottom:12px
    .buy
      position:absolute
      right:18px
      bottom:18px
      z-index:10
      height:24px
      line-height:24px
      padding:0 12px
      box-sizing:border-box
      font-size:10px
      border-radius:12px
      color:#fff
      background:rgb(0,160,220)
  .info
    padding:18px
    .title
      line-height:14px
      margin-bottom:6px
      font-size:14px
      color:rgb(77,85,93)  
    .text
      line-height:24px
      padding:0 8px
      font-size:12px
      color:rgb(77,85,93)
  .rating
    padding-top:18px
    .title
      line-height:14px
      margin-left:18px
      font-size:14px
      color:rgb(77,85,93) 
    .rating-wrapper
      padding:0 18px
      .rating-item
        position:relative
        padding:16px 0
        border-1px(rgba(7,17,27,0.1))
        .user
          position:absolute
          top:16px
          right:0
          line-height:12px
          font-size:0
        .name
          display:inline-block
          vertical-align:top
          margin-right:6px
          font-size:10px
          color:rgb(147,153,159)
        .avatar
          border-radius:50%
        .time
          margin-bottm:6px
          line-height:12px
          font-size:10px
          color:rgb(7,17,27)
        .text
          line-height:16px
          font-size:12px
          color:rgb(7,17,27)
          .icon-thumb_down
          .icon-thumb_up
            margin-right:4px
            line-height:24px
            font-size:12px
          .icon-thumb_up
            color:rgb(0,160,220)
          .icon-thumb_down
            color:rgb(147,153,159)
      .no-rating
        padding:16px 0
        font-size:12px
        color:rgb(147,153,159)

</style>



